var grid = new Float32Array(8);
var edges = new Int32Array(12);
var x_a =  new Float32Array(3);
var mmscale     = new Float32Array(3);//[0,0,0];
var mmshift     = new Float32Array(3);

var lastPrintOutTime = Date.now();

var _marchingCubes = function(potential, setOfBlocks, doPrintProgress=false) {
    var s=0;
    var cube_index=0;
    var faces = [];
    var vertices = [];
    var startTime = Date.now();

    for(var f=0; f<setOfBlocks.length; f++) {

        if (doPrintProgress && (Date.now()-lastPrintOutTime)>1000) {
            console.log(`marching cubes: ${f} of ${setOfBlocks.length} -- ${(f/setOfBlocks.length*100.0).toFixed(2)}% done`);
            lastPrintOutTime = Date.now();
        }

        var bounds = setOfBlocks[f];

        mmscale[0] = (bounds[1][0] - bounds[0][0]);
        mmshift[0] = bounds[0][0];
        mmscale[1] = (bounds[1][1] - bounds[0][1]);
        mmshift[1] = bounds[0][1];
        mmscale[2] = (bounds[1][2] - bounds[0][2]);
        mmshift[2] = bounds[0][2];

        x_a[2] = 0;
        x_a[1] = 0;
        x_a[0] = 0;

        var cube_index = 0;
        for(var i=0; i<8; ++i) {
            var v = cubeVerts[i]
                , s = potential(
                mmscale[0]*(x_a[0]+v[0])+mmshift[0],
                mmscale[1]*(x_a[1]+v[1])+mmshift[1],
                mmscale[2]*(x_a[2]+v[2])+mmshift[2]);
            grid[i] = s;
            cube_index |= (s > 0) ? 1 << i : 0;
        }

        //Compute vertices
        var edge_mask = edgeTable[cube_index];
        if(edge_mask === 0) {
            continue;
        }
        for(var i=0; i<12; ++i) {
            if((edge_mask & (1<<i)) === 0) {
                continue;
            }
            edges[i] = vertices.length;
            var nv = [0,0,0]
                , e = edgeIndex[i]
                , p0 = cubeVerts[e[0]]
                , p1 = cubeVerts[e[1]]
                , a = grid[e[0]]
                , b = grid[e[1]]
                , d = a - b
                , t = 0;
            if(Math.abs(d) > 1e-6) {
                t = a / d;
            }
            for(var j=0; j<3; ++j) {
                nv[j] = mmscale[j] * ((x_a[j] + p0[j]) + t * (p1[j] - p0[j])) + mmshift[j];
            }
            vertices.push(nv);
        }
        //Add faces
        var _f = triTable[cube_index];
        for(var i=0; i<_f.length; i += 3) {
            faces.push([edges[_f[i]], edges[_f[i+1]], edges[_f[i+2]]]);
        }

    }
    if(doPrintProgress){
        var timeSec = (Date.now()-startTime)/1000;
        console.log(`marching cubes: done after ${(timeSec).toFixed(2)} seconds; ${(setOfBlocks.length/(timeSec+0.00001)).toFixed(2)} voxels per second`);
    }
    return {
        positions: vertices,
        cells: faces
    }
};

module.exports = {marchingCubes: _marchingCubes}

